.progress {
    -webkit-transform: rotate(-90deg);
        -ms-transform: rotate(-90deg);
            transform: rotate(-90deg);
    height: 12px;
    width: 12px;
}

.unit-body-wrap { 
    padding-right: 15px;
}

.canvas {
    z-index: 500;
    pointer-events:none;
    position: absolute;
}
.linesCanvas {
    top: 0px;
    left: 0px;
    z-index: 400;
    pointer-events:none;
    position: absolute;
}
.bulletCanvas {
    top: 0px;
    left: 0px;
    z-index: 500;
    pointer-events:none;
    position: absolute;
}
.shootingLevelCanvas {
    top: 0px;
    left: 0px;
    z-index: 999999;
    pointer-events:none;
    position: absolute;
}

.linesSvg {
    z-index: 10000;
    position: absolute;
    pointer-events:none;
}

.editUnitForm {
    position: absolute;
    top: 0px;
    right: 0px;
    color: white;
    width: 150px;
    background-color: black;
    padding: 20px;
    opacity: 0.8;
}
.scenarioChooser {
    position: absolute;
    top: 0px;
    left: 0px;
    color: white;
    width: 150px;
    background-color: black;
    padding: 20px;
    opacity: 0.8;
}

.scenarioChooser ul {
    list-style-type: none;
    margin: 0px;
    padding: 0px;
}
.scenarioChooser li {
    cursor: pointer;
    text-decoration: underline;
    margin-bottom: 10px;
}

.fps {
    position: absolute;
    top: 10px;
    right: 10px;
    color: white;
}

.timeElapsed {
    position: absolute;
    padding: 10px;
    width: 100px;
    top: 10px;
    margin-left: auto;
    margin-right: auto;
    left: 0px;
    right: 0px;
    font-size: 24pt;
    color: white;
    z-index: 999999;
}

.circularBarContainer{ 
    padding-left: 5px;
    width: 12px;
    height: 12px;
    margin-top: -3px;
}

.progress__value {
    /* stroke-dasharray: 339.292;
    stroke-dashoffset: 339.292; */
    fill: none;
    stroke: orange;
    stroke-width: 2;
}

.progress__back {
    fill: none;
    stroke: #444444;
    stroke-width: 2;
}

.unitStatus {
    display:-ms-flexbox;
    display:flex;
    margin-bottom: 5px;
    min-width: 10px;
}

html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  height: 100%;
  background-color: #222222;
  /** background-image:linear-gradient(#383838, #0e0e0e); **/
}


.board {
    position: relative;
    margin: 0 auto;
    -webkit-transition: -webkit-transform 1s ease 0s;
    transition: -webkit-transform 1s ease 0s;
    -o-transition: transform 1s ease 0s;
    transition: transform 1s ease 0s;
    transition: transform 1s ease 0s, -webkit-transform 1s ease 0s;
}

.board {
    height:100%;
    background-size:80px 80px;
    background-color: transparent;
    background-image:       -webkit-gradient(linear, left bottom, left top, color-stop(24%, transparent), color-stop(25%, rgba(255, 255, 255, .2)), color-stop(26%, rgba(255, 255, 255, .2)), color-stop(27%, transparent), color-stop(74%, transparent), color-stop(75%, rgba(255, 255, 255, .2)), color-stop(76%, rgba(255, 255, 255, .2)), color-stop(77%, transparent), to(transparent)), -webkit-gradient(linear, left top, right top, color-stop(24%, transparent), color-stop(25%, rgba(255, 255, 255, .2)), color-stop(26%, rgba(255, 255, 255, .2)), color-stop(27%, transparent), color-stop(74%, transparent), color-stop(75%, rgba(255, 255, 255, .2)), color-stop(76%, rgba(255, 255, 255, .2)), color-stop(77%, transparent), to(transparent));
    background-image:       -webkit-linear-gradient(bottom, transparent 24%, rgba(255, 255, 255, .2) 25%, rgba(255, 255, 255, .2) 26%, transparent 27%, transparent 74%, rgba(255, 255, 255, .2) 75%, rgba(255, 255, 255, .2) 76%, transparent 77%, transparent), -webkit-linear-gradient(left, transparent 24%, rgba(255, 255, 255, .2) 25%, rgba(255, 255, 255, .2) 26%, transparent 27%, transparent 74%, rgba(255, 255, 255, .2) 75%, rgba(255, 255, 255, .2) 76%, transparent 77%, transparent);
    background-image:       -o-linear-gradient(bottom, transparent 24%, rgba(255, 255, 255, .2) 25%, rgba(255, 255, 255, .2) 26%, transparent 27%, transparent 74%, rgba(255, 255, 255, .2) 75%, rgba(255, 255, 255, .2) 76%, transparent 77%, transparent), -o-linear-gradient(left, transparent 24%, rgba(255, 255, 255, .2) 25%, rgba(255, 255, 255, .2) 26%, transparent 27%, transparent 74%, rgba(255, 255, 255, .2) 75%, rgba(255, 255, 255, .2) 76%, transparent 77%, transparent);
    background-image:       linear-gradient(0deg, transparent 24%, rgba(255, 255, 255, .2) 25%, rgba(255, 255, 255, .2) 26%, transparent 27%, transparent 74%, rgba(255, 255, 255, .2) 75%, rgba(255, 255, 255, .2) 76%, transparent 77%, transparent), linear-gradient(90deg, transparent 24%, rgba(255, 255, 255, .2) 25%, rgba(255, 255, 255, .2) 26%, transparent 27%, transparent 74%, rgba(255, 255, 255, .2) 75%, rgba(255, 255, 255, .2) 76%, transparent 77%, transparent);       
}

.unit {
    will-change: transform;
    display: inline-block;
    position: absolute;
    margin-left: -25px;
    margin-right: -25px;
    margin-top: -35px;
    margin-bottom: -35px;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    z-index: 100000;
}

.item {
    display: inline-block;
    position: absolute;
}


.bullet {
    display: inline-block;
    position: absolute;
    height: 2px;
    width: 2px;
    background-color: white;
}

.bar {
    min-height: 4px;
    background-color: #555555;
    margin: 0 auto;
    margin-bottom: 1px;
}

.bar .level {
    min-height: 4px;
    -webkit-transform-origin: 0px;
        -ms-transform-origin: 0px;
            transform-origin: 0px;
}

div.unit-body {
    border-radius: 5px; 
    background-color: black;
    padding: 4px;
    font-size: 14pt;
    display: block;
    width: 30px;
    text-align: center;
    margin: 0 auto;
    /* transition: transform 0.1s ease 0s; */
}

div.unit-red {
    color: #FF8888;
    color:white; 
     border: 3px solid #FF8888; 
}

.unit-blue {
    color: #8888FF;
    color:white; 
    border: 3px solid #8888FF; 
}


.line {
    stroke-width: 2;
    stroke-dasharray: 2, 6;
    opacity: 0.7;
    /* will-change: transform; */
}

.line.selected {
    stroke-width: 10;
    stroke-dasharray: 10, 4;
    opacity: 0.4;
}

.line.unit-red {
    stroke: #FF8888;
}

.line.unit-blue {
    stroke: #8888FF;
}

.time {
    padding: 10px;
    color: white;
}

.description {
    color: white;
}

.controls {
    padding-bottom: 10px;
    font-size: 18pt;
    z-index: 1000;
    position: absolute;
    bottom: 0px;
    left: 0px;
    right: 0px; 
    margin-left: auto;
    margin-right: auto;
    min-width: 600px;
    width: 62vw;
}

.controls button {
    opacity: 0.6;
    color: white;
    border: 1px solid #888888;
    padding: 10px;
    font-size: 16pt;
    margin-right: 10px;
    text-decoration: none;
    background: #888888;
    margin-right: 20px;
    width: 6vw;
    min-width: 60px;
}
.controls button:hover {
    border: 1px solid white;
    opacity: 1;
}

.controls span {
    font-size: 10pt;
    color: white;
}

.circle {
    z-index: -9999;
}

.distanceCircle {
    stroke-width: 2px;
    stroke-dasharray: 2, 6;
    stroke: white;
    z-index: -9999;
    fill: #1A1A1A;
/* fill={{ color: 'rgba(200,200,200,0.05)' }}
stroke={{ color: 'rgba(0,0,0,0.1)' }}
strokeWidth={1} /> */
}

.distanceCircle.small {
}
.distanceCircle.medium {
    z-index: -9999;
}
.distanceCircle.large {
    opacity: 0.6;
}
.unit-body.short {
    background-color: #6c1717!important;
}
.unit-body.medium {
    background-color: #12424d!important;
}
.unit-body.long {
    background-color: #173f17!important;
}


